import React from 'react';
import { useContext } from 'react';
import { ProjectContext } from '../../context/ProjectContext';
import { SearchContext } from '../../context/SearchContext';
import { navigate } from 'gatsby';
const Suggestions = () => {
  const { sponsoredListings } = useContext(ProjectContext);
  const { setSearchValue } = useContext(SearchContext);
  const handleSearchChange = (value: string) => {
    setSearchValue(value);
    navigate(`/search-results?search-query=${value}`);
  };

  return (
    <div className='w-[25%] h-fit mt-12 p-6 rounded-md mr-4 shadow-sm border-[1px] hidden md:block'>
      <h2 className='font-bold text-xl'>Sponsored Listings</h2>
      {
        sponsoredListings?.map((listing) => (
          <a href={`${listing.links.websiteLink}`} target='_blank' key={`listing-${listing._id}`} className='flex space-x-3 mt-8 border-b-[1px] pb-6 '>
            <img src={listing.logo} className='h-12 rounded-full' />
            <div className='w-[-webkit-fill-available]'>
              <h2 className='font-bold text-lg'>{listing.name}</h2>
              <p className=''>{listing.tldr}</p>
            </div>
          </a>
        ))
      }
      <div className='flex space-x-2 justify-center mt-8'>
        <button onClick={() => handleSearchChange('Layer 1')} className='w-32 border-[1px] border-primarydark font-semibold rounded-md p-2'>View all</button>
        <button onClick={() => navigate('/add-project')} className='bg-primarydark text-white font-semibold w-32 rounded-md p-2'>List a project</button>
      </div>
    </div>
  )
}
export default Suggestions;
